.header__sidebar{
    
    max-width: 317px;
    transition: all .5s linear;
    display: flex;
    position: fixed;
    top: 0;
    bottom: 0;
    float: left;
    overflow-y: scroll;
    -ms-overflow-style: none;
    overflow: -moz-scrollbars-none;

    &.hiddden{
        transform: translateX(-317px);
    }
    &::-webkit-scrollbar{
        width: 0;
    }
    
}
.sidebar__fixed{
    position: fixed !important;
}
.sidebar__wrapper{

    
    display: flex;
    flex-direction: column;
}
.sidebar__logo{
    display: flex;
    align-items: center;
}
.user{
    display: flex;
    align-items: center;
    margin-top: 39px;
}

.user__more{
    margin-top: 8px;
   margin-left: 5px;
    background: #1B69FF;
    border-radius: 12px;
    border:none;
    height: 17px;
    margin-bottom: 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    
   
}
.user__ellipsis-item{
    width: 4px;
    height: 4px;
    background-color: #fff;
    border-radius: 50%;
    &:nth-child(2){
        margin:0 3px;
    }
    
}
.user__ellipsis{
    display: flex;
}
.user__name{
    margin-right: 10px;
    justify-content: space-between;
    display: flex;
    align-items: center;
    
}
.user-name__wrapper{
    margin-left: 22px;
    max-width: 150px;
}
.main__menu-item{
    
    display: flex;
    align-items: center;
    cursor: pointer;
    font-size: 16px;
    margin-bottom: 10px;
    height: 53px;
    margin-left: -30px;
    max-width: 248px;
    padding-left: 30px;
    color: #2D2D2D;
    position: relative;
    border-radius: 12px;


    img{
        margin-right: 23px;
    }
    &.active{
        background: #fff;
        color: #1B69FF;
        &::before{
            position: absolute;
            height: 53px;
            width: 5px;
            content: "";
            background: #1B69FF;
            border-radius: 0px 12px 12px 0px;
            left: -30px;
        }
    }
    &:hover{
        background: #fff;
        color: #1B69FF;
    }
}
            
.header-cabinet__wrapper{
    z-index: 100;
    position: fixed;
    top: 0;
    right: 0;
    height: 60px;
    padding: 30px 30px 80px 40px;
    width: calc(100% - 290px);
    background: #F4F4F8;
    display: flex;
    justify-content: space-between;
    transition: all .5s linear;

    
}
.header__hidden{
    width: 97% !important;
}
.header-cabinet__settings{
    display: flex;
}
.cabinet__header-wrapper{
    display: flex;

}
.cabinet__header-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 12px;
    border: 1px solid #2D2D2D;
    background: transparent;
    width: 44px;
    height: 44px;
    cursor: pointer;
    transition: all .5s linear;
    
}
.header__logo-mobile{
    display: none;
}


.arrow__rotate{
    transform: rotate(180deg);
}
.header-cabinet-btn{
    width: 44px;
    height: 44px;
    background: #E4E4F1;
    border:none;
    border-radius: 12px;
    margin-left: 12px;

    &--out{
        background: #2D2D2D;
    }
}
.main__content{
    transition: all .5s linear;
    float: right;
    width: calc(100% - 330px);
    margin-top: 115px;

    &--table{
        margin-top: 40px;
    }
}
.mobile__link{
    display: none;
}

.burger{
    display: flex;
    width: 44px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #E4E4F1;
    border-radius: 15px;
    margin-left: 30px;
    flex-direction: column;
    cursor: pointer;
}
.burger__line{
    width: 18px;
    height: 3px;
    background: #9191B2;
    margin-bottom: 3px;
    border-radius: 30px;
}
.burger__menu{
    position: relative;
    margin-top: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    display: none;
    
}
.mobile__sidebar{
    overflow: scroll;
    position: absolute;
    width: 282px;
    height: 100vh;
    z-index: 100;
    right: -30px;
    background: #FFFFFF;
    box-shadow: 0px 4px 50px #E7E7F3;
    border-radius: 0px 0px 0px 40px;
    padding-left: 19px;
    padding-bottom: 19px;
    top: -60px;
    transition: all .3s linear;
    transform: translateX(282px);


    .main__menu{
        margin-left: 20px;
        .main__menu-item.active::before{
            display: none;
        }
    }
    
}
.mobile__sidebar--active{
    transform: translateX(0);
}
.mobile__siderbar-settings{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 40px;
}
.mobile__sidebar-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    background: #E4E4F1;
    width: 44px;
    height: 44px;
    border-radius: 10px;

    &--close{
        background: transparent;
        border: 1px solid #9191B2;
        border-radius: 15px;
        margin-left: 16px;
        margin-right: 30px;
    }
}


@media (max-width: 768px){
    .header__logo-mobile{
        display: flex;
    }
    .cabinet__header-btn{
        display: none;
    }
    .header-cabinet__settings{
        display: none;
    }
    .burger__menu{
        display: flex;
    }
}
