@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

//   
@import "./components/header";
@import "./components/footer";
@import "./components/modal";
@import "./components/sidebar";
@import "./components/cabinet";




.main-screen__wrapper{
    display: flex;
    justify-content: space-between;
}
.main-screen__text{
    max-width: 50%;
}
.main-screen__title{
    font-size: 55px;
    line-height: 65px;
    color: #2D2D2D;
}
.main-screen__description{
    margin: 21px 0 64px 0;
    max-width: 350px;
    font-size: 22px;
    line-height: 33px;
    color: #2D2D2D;
   span{
       color: #1B68FF;
   } 


}
.btn__mobile{
  display: none;
}
.main-screen__buttons{
    display: flex;
}

.center {
    width: 100%;
    max-width: 980px;
    margin: auto;
  }
  
  #video {
    width: 100%;
    position: relative;
    
  
    &-over {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 1;
      display: none;
    }
  
    &-play {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      width: 80px;
      height: 80px;
      border-radius: 50%;
      border:none;
      background: #1B68FF;
      
      z-index: 1;
      cursor: pointer;
      transition: .2s ease;
      transition-property: opacity, visibility;
  
      &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        left: 32px;
        width: 0;
        height: 0;
        border: 20px solid transparent;
        border-top-width: 12px;
        border-bottom-width: 12px;
        border-left-color: #fff;
        border-right-width: 0;
        transition: transform .2s ease;
      }
  
      &:hover {
        &::before {
          transform: scale(1.2);
        }
      }
    }
  
    video {
      width: 100%;
      display: block;
      box-shadow: 0px 20px 50px rgba(27, 105, 255, 0.04);
      border-radius: 12px;
      
    }
  
    &.video-is-playing {
      #video {
        &-over {
          display: block;
        }
  
        &-play {
          opacity: 0;
          visibility: hidden;
        }
      }
    }
  }
  .about-company__wrapper{
      display: flex;
      flex-direction: column;
      align-items: center;
      
  }


.road-map__title{
    text-align: center;
}
.road-map__block{
    display: flex;
    flex-wrap: wrap;
    

    &--mb{
        margin-bottom: 160px;
        position: relative;
    }
    
}
.road-map__bg{
    
        position: absolute;
    bottom: -80px;
    left: 100px;
    
}
.road-map__block-date{
    font-size: 96px;
    line-height: 60px;
    color: #1B69FF;
    width: 269px;
    

}
.road-map__item{
    max-width: 269px;
    

    &--ml{
        margin-left: 120px;
        margin-bottom: 72px;
    }
    
}
.road-map__item-title{
    display: flex;
    align-items: center;
}
.road-map__item-number{
    background: #2D2D2D;
    border-radius: 12px;
    width: 35px;
    height: 35px;
    color:#fff;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 19px;

    &--in-progress{
        background: #9191B2;
    }
}

.road-map__item-month{
    font-size: 14px;
    line-height: 27px;
    letter-spacing: 0.195em;
    text-transform: uppercase;
    color: #2D2D2D;

    &--in-progress{
        color: #9191B2;
    }
}
.road-map__item-descripion{
    font-size: 14px;
    line-height: 24px;
    color: #7A7A88;
    margin-top: 24px;

    &--in-progress{
        color: #9191B2;
    }

}
.main-screen__buttons--mobile{
  display: none;
}
.btn--sign-in-mobile{
  display: none;
}

@media (max-width: 768px){
  .header__social{
    margin-left: 0;
  }
  .road-map__item{
    margin-right: 0px;
    &--mr{
      margin-right: 30px;
    }
  }
  .header__wrapper{
    padding: 49px 0;
    
  }

  .main-screen__title{
    font-size: 52px;
    max-width: 200px;
  }
  .road-map__block-date{
    font-size: 80px;
    width: 260px;

  }
  .road-map__item--ml{
    margin-left: 0;
  }
  .road-map__item-descripion{
    max-width: 235px;
  }
  .road-map__bg{
    display: none;
  }
  
}


@media (max-width: 600px){
  .header__social-name{
    display: none;
  }
  .main-screen__buttons{
    display: none;
    &--mobile{
      margin-top: 30px;
      display: flex;
    }
  }
   
   

  .btn--blue-mobile{
    width: 154px;
    height: 60px;
    margin-right: 5px;
  }
  .road-map__block{
    margin-bottom: 50px;
  }
  
  .btn {
    
    &--mobile{
      height: 44px;
      display: block;
      img{
        margin-right: 0;
      }
    }
  }
  .main-screen__wrapper{
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .main-screen__title{
    font-size: 27px;
    margin-right: 0;
    text-align: center;
    line-height: 42px;
    max-width: 375px;
  }
  .main-screen__text {
    max-width: 100%;
} 
  .main-screen__description{
    text-align: center;
    font-size: 18px;
  }
  .road-map__block-date{
    width: 100%;
    text-align: center;
    margin-bottom: 34px;
  }
  .section__title{
    font-size: 24px;
  }
  .road-map__item-descripion{
    max-width: 375px;
  }
  .road-map__item--mr{
    margin-bottom: 20px;
  }
  .footer__wrapper{
    flex-direction: column;
    justify-content: flex-start;
  }
  .footer-info{
    margin: 20px 0;
  }
  .header__social{
    display: none;
  }
  .btn--sign-in{
    display: none;
  }

  .btn--sign-in-mobile{
    display: flex;
  }
  
  .btn__mobile{
    display: flex;
    img{
      margin-right: 5px;
    }
  }
  .btn__main-screen{
    max-width: 135px;
    height: 55px;
    img{
      margin-right: 5px;
    }
  }
}
