.main__content{
    background: #FFFFFF;
    width: calc(100% - 330px);
    color: #fff;
    border-radius: 30px 0px 0px 30px;
    padding:42px;

    &--partners{
        margin-top: 0;
        background: transparent;
        padding: 0;
    }

    
}
.main__table-mobile{
    display: none;
}
.main__content-header{
    align-items: flex-start;
    display: flex;
    justify-content: space-between;
    

}
.main__content-breadcrumb{
    display: flex;
    align-items: center;
}
.main__content-title{
    font-size: 36px;
    line-height: 27px;
    color: #2D2D2D;
    display: flex;
    
    &--transactions{
        font-size: 24px;
        margin-bottom: -27px;
    }
}
.main__content-home{
    margin-right: 47px;
    position: relative;

    a{
        font-size: 14px;
        line-height: 27px;
        color: #9191B2;


        &:hover{
            text-decoration: underline;
        }
    }

    &::after{
        content: "";
        position: absolute;
        width: 23px;
        height: 2px;
        background: #E4E4F1;
        top: 50%;
        transform: translateY(-50%);
        right: -37px;
    }
    &--trans{
        margin-right: 12px;
        &::after{
            display: none;
        }
    }
}
.main__content-current{
    font-size: 14px;
line-height: 27px;



color: #2D2D2D;

}

.clearfix__main-card{
    background: linear-gradient(77.37deg, #2374EE 14.31%, #257CFF 38.59%, #436DFF 59.5%, #5F82FF 94.63%);
box-shadow: 0px 8px 17px rgba(27, 105, 255, 0.22);
border-radius: 15px;
max-width: 369px;

}
.clearfix__card-header{
    display: flex;
    padding: 18px 32px;
    justify-content: space-between;
}
.exchange-money{
    img{
        margin-right: 14px;
    }
}
.clearfix__card-main-temp{
    padding: 0 32px 0px 32px;
    font-size: 55px;
    
    display: flex;
    
    span{
        font-weight: 500;
        font-size: 24px;
        margin-top: 6px;
        margin-left: 5px;



    }
}
.first__row{
    margin-top: 40px;
    display: flex;
    
}
.clearfix__button-block{
    border-top: 1px solid #1D62E9;
    display: flex;
    justify-content: space-between;
}
.clearfix-btn{
    color:#fff;
    display: flex;
    align-items: center;
    background: transparent;
    border:none;
    font-size: 14px;
    line-height: 27px;
    cursor: pointer;
    padding: 18px 32px;


color: #FFFFFF;
img{
    margin-right: 18px;
}
}
.clearfix__left-btn{
    padding-right: 10px;
    border-right: 1px solid #1D62E9;
}
.clearfix__card{
    background: linear-gradient(360deg, #EBF2FF 15.49%, #F8FAFF 105.71%);
    border-radius: 15px;
    color: #000;
    margin: 0 15px;
}
.clearfix__card-title{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 37px 56px 24px 56px;
    img{
        margin-bottom: 13px;
    }
}
.clearfix__card-temp{
    border-top: 1px solid #E5EEFE;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 20px;
    font-size: 24px;
    line-height: 27px;
    color: #1B69FF;
    span{
        font-size: 12px;
        margin-top: -4px;
    }
}
.second__row{
    margin-top: 35px;
   
}
.clearfix__user-info{
    display: flex;
}
.clearfix__info-card{
    padding:14px 16px 34px 33px;
    color:#000;
    border: 1px solid #F1F1FD;
    box-sizing: border-box;
    border-radius: 15px;
    margin-right: 13px;

    &--partners{
        background: #fff;
        margin-right: 29px;
    }
}
.clearfix__info-header{
    display: flex;
    align-items: center;
}
.clearfix__info-title{
    margin-right: 52px;
}
.clearfix__info-main-temp{
    display: flex;
    
    font-weight: bold;
font-size: 48px;
line-height: 27px;


margin-top: 21px;
&--transitions{
    color: #D7D7EB;
}
color: #1B69FF;
    span{
        font-weight: 300;
        font-size: 24px;
        margin-top: -10px;
    }






    }
    .main__content-body{
        position: relative;
    }
.main__table{
    
    color: #000;
    width: 100%;
    text-align: left;
    margin-top: 27px;
    tr {
        height: 40px;
        margin-bottom: 33px;
        td{
            font-size: 14px;
            line-height: 27px;
            color: #2D2D2D;
        }
    }
   
}
.main__table-title{
    
    th{
        font-size: 14px;
        line-height: 27px;
        color: #9191B2;
    }
}
.sucsess{
    color: green !important;

    &--not{
        color: red !important;
    }
}
.td__hr{
    position: absolute;
    top: 69px;
    width: 100%;
    height: 1px;
    background: #F1F1FD;;
}
.main__content-body{
    color: #000;
    display: flex;
    align-items: center;
    margin-top: 33px;
}
.main__content-body-form{
    display: flex;
}
.main__content-select{
    font-size: 14px;
    
    color: #2D2D2D;
    border: 1px solid #D7D7EB;
    box-sizing: border-box;
    border-radius: 12px;
    min-width: 253px;
    cursor: pointer;
    padding: 0 32px;
    appearance: none;
    height: 53px;
    margin-right: 12px;
    
}

.main__content-input{
    background: #F4F4F8;
    border: 1px solid #ECECFF;
    height: 53px;
    border-radius: 12px;
    padding: 0 32px;
    outline-style:1px solid #1B69FF;
   

}
.btn{
    &--cabinet{
        font-weight: 500;
        font-size: 18px;
        margin-left: 32px;
        color: #FFFFFF;
        width: 242px;
    }
}

.main__content-wrapper{
    &--transactions{
        margin-top: 52px;
    }
}
.requisites{
    color: #000;
}
.requisites__item{
    margin-top: 30px;
    display: flex;
    flex-direction: column;

    &:last-child{
        margin-bottom: 24px;
    }
}
.requisites__input{
    margin-top: 5px;
    width: 587px;
    background: #F4F4F8;
    border: 1px solid #ECECFF;
    box-sizing: border-box;
    border-radius: 12px;
    height: 53px;
    padding: 0 22px;

    &::placeholder{
        font-size: 14px;
        color: #C5C5D8;
    }
}
.main__content-body{
   &--partners{
       display: block;
   }
}
.main__content-body-title{
    font-size: 14px;
    line-height: 27px;
    color: #9191B2;
}
.main__content-link{
    display: flex;
    align-items: center;
    border:none;
    outline: none;
    font-weight: 500;
    font-size: 24px;
    
    color: #1B69FF;
    button{
        margin-left: 27px;
    }
}
.main__content-tabs{
    display: flex;
    margin-left: 40px;
    
}
.main__content-roadmap-item{
    display: flex;
    margin-bottom: 50px;
    &:last-child{
        .roadmap__item-date-circle{
            &::after{
                display: none;
            }
        }
    }
   
}
.roadmap__item-date{
    display: flex;
    align-items: center;
    width: 125px;
}
.roadmap__item-date-circle{
    width: 22px;
    height: 22px;
    background: transparent;
    border-radius: 50%;
    border: 3px solid #1B69FF;
    margin-right: 20px;
    position: relative;

    &--orange{
        border: 3px solid #FFA077;
    }
    &--green {
        border: 3px solid #3ED668;
    }
    &::after{
        content: "";
        top: 40px;
        left: 50%;
        transform: translateX(-50%);
        position: absolute;
        background: #D7D7EB;
        width: 2px;
        height: 300%;
    }
    
}
.roadmap__item-date-name{
    line-height: 27px;
    color: #9191B2;

}
.roadmap__item-description{
    padding-top: 18px;
    max-width: 741px;
    margin-left: 60px;
    line-height: 24px;
    color: #2D2D2D;
    font-size: 14px;
    
}
@media (max-width: 1440px){


    .clearfix{
        flex-wrap: wrap;
    }
    .clearfix__user-info{
        flex-wrap: wrap;
    }
}
@media (max-width: 1439px){
    .clearfix__card{
        margin-left: 0;
        margin-right: 10px;
        height: 181px;
        
    }
    .clearfix__main-card{
        margin-bottom: 20px;
        margin-right: 20px;
    }
    .clearfix__info-card{
        margin-bottom: 10px;
    }
    .main__content-body-form {
       
        flex-wrap: wrap;
    }
    .btn--cabinet{
        margin:0;
        width: 253px;
    }
    .main__content-input{
        width: 253px;
        margin-right: 15px;
    }
    select{
        margin-bottom: 15px;
    }
    .roadmap__item-description{
        width: 900px;
    }
}
@media (max-width: 768px){
    .roadmap__item-description{
        width: 600px;
    }
    .header__sidebar {
        display: none;
    }
    .main__content{
        width: 100%;
    }
    .header-cabinet__wrapper{
        width: 100%;
    }
    select {
        width: 100%;
    }
    .main__content-input{
        width: 96%;
        margin-right: 0;
    }
    .btn--cabinet{
        width: 100%;
        margin-top: 0;
        
    }
    .requisites__input{
        max-width: 100%;
    }
    .clearfix__info-card--partners{
        width: 45%;
    }
    .btn--partners__tabs{
        padding: 5px 0px;
        width: 81px;
        height: 36px;
    }
    .btn--cabinet{
        margin-top: 15px;
    }
}

@media (max-width: 500px){
    .clearfix__main-card{
        width: 100%;
        margin-right: 0;
    }
    .clearfix__card{
        width: 100%;
        margin-bottom: 20px;
    }
    .clearfix__info-card{
        width: 100%;
        
    }
    .clearfix__info-header{
        width: 100%;
        justify-content: space-between;
    }
    .main__content-header{
        flex-direction: column;
    }
    .main__content-title{
        order: 2;
    }
    .main__content-breadcrumb{
        order: 1;
    }
    .roadmap__item-description{
        margin-left: 20px;
        max-width: 220px;
    }
    
}

@media (max-width: 350px){
    .main__content-input{
        width: 96%;
        
    }
    select{
        width: 84%;
    }
    
    .btn--cabinet{
        width: 256px;
    }
}

@media (max-width: 414px){
    .main__content-header{
        flex-direction: column;
        align-items: flex-start;

        &--table{
            flex-direction: row;
        }
        &--cabinet{
            flex-direction: column;
        }
    }
    .td__hr {
        display: none;
    }
    .main__content-title{
        order: 2;
    }
    .exchange-money{
        font-size: 12px;
    line-height: 18px;
    }
    .clearfix__card-header-tutle{
        font-size: 15px;
    line-height: 27px;
    }
    .clearfix__card-header {
        
        padding: 16px 16px;
        
    }
    .exchange-money img {
        margin-right: 7px;
    }
    .clearfix__card-main-temp {
        padding: 0 16px 0px 16px;
        
    }
    .clearfix__left-btn{
        padding-right: 0;
    }
    .clearfix-btn{
        padding: 16px 16px;
    }
    .clearfix__card-title {
        
        padding: 32px 25px 24px 22px;
    }
    .clearfix__info-card{
        width: 100%;
    }
    
    .clearfix__info-header {
    
        justify-content: space-between;
    }
    .main__content-wrapper{
        display: flex;
        flex-direction: column;
    }
    .main__content-title{
        font-size: 18px;
        line-height: 27px;
    }
    input{
        margin-bottom: 15px;
    }
    .main__content {
        
        padding: 29px;
    }
    .clearfix__card{
        width: 100%;
        margin-top: 15px;
        margin-right: 0;
    }
    .clearfix__main-card{
        margin:0;
        width: 100%;
    }
    .clearfix__info-card{
        margin-right: 0;
    }
    
   
    .main__table {
        
        overflow: scroll;
    }
    .main__table-descktop{
        display: none;
    }
    .main__table-mobile{
        margin-top: 0;
        display: flex;
        tr{
            margin-bottom: 20px;
            td{
                margin-right: 25px;
            }
        }
    }
    .main__content-title--partners{
        flex-direction: column;
    }
    .main__content-title{
        
       
      
        width: 100%;
        
    }
    .btn--partners__tabs {
       
        width: 67px;
        
    }
    .main__content-link {
        
        font-size: 13px;
      
            align-items: flex-end; 

    
    }
    .main__content-tabs{
        margin-top: 20px;
        margin-left: 0;
    }
    .btn--partners{
        margin-right: 2px;
    }
    .btn--blue{
        margin-right: 2px;
    }
    .main__content-roadmap-item{
        flex-direction: column;
    }
    .roadmap__item-description{
        margin-left: 0;
        max-width: 269px;
    }
    .roadmap__item-date-circle{
        &::after{
            display: none;
        }
    }
    .main__content-breadcrumb--roadmap{
        display: none;
    }
    .main__content-wrapper--roadmap{
        margin-top: 0;
    }
    .main__content-title--roadmap{
        margin:20px 0;
    }
    .user__avatar-header{
        display: none;
    }
    .clearfix__button-block{
        justify-content: center;
    }
}
