.header__wrapper{
  padding: 88px 0;
  display: flex;
  justify-content: space-between;
}
.header__logo{
  display: flex;
  align-items: center;
}
.header__logo-name{
 
  margin-left: 15px;
  max-width: 95px;
  text-transform: none;
  color: #202020;
  text-decoration: none;
  
  span{
    text-transform: uppercase;
    font-weight: 900;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.215em;
    color: #202020;
  }
  &--footer{
    color: #9191B2;

    span{
      color: inherit;
    }
  }
}
.header__social{
  display: flex;
  align-items: center;
  margin-left: 200px;
}
.header__social-name{
  margin-right: 24px;
  font-size: 14px;
  line-height: 20px;
  &--footer{
    color:#9191B2;
  }


}
.header__social-icon-block{
  display: flex;
}
.header__social-icon{
  background: #FFFFFF;
  box-shadow: 0px 20px 50px rgba(27, 105, 255, 0.04);
  border-radius: 12px;
  margin-left: 6px;
  width: 44px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;

  &--footer {
    background: transparent;
    border: 1px solid #9191B2;
  }
  
}

