.modal__logo{
    display: flex;
    align-items: center;
    margin-top: 87px;
    
}
.modal__left-side{
    display: flex;
    flex-direction: column;
    border-right: 1px solid #F1F1FD;
    padding: 44px 60px;
}
.sign-up__wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.modal__wrapper{
    display: flex;
    margin-top: 71px;
    background: #FFFFFF;
    box-shadow: 0px 4px 208px #E7E7F3;
    border-radius: 30px;
    
}
.modal__left-description{
    margin-top: 20px;
    max-width: 400px;
    font-size: 20px;
    line-height: 33px;
    color: #2D2D2D;
    margin-right: 100px;

}
.modal__left-title{
    max-width: 250px;
    font-size: 48px;
    line-height: 60px;
    color: #2D2D2D;

}
.modal__left-actions{
    display: flex;
    position: relative;
    bottom: 0;
    margin-top: auto;
    
    align-items: center;
    a{
        span{
            font-size: 14px;
            line-height: 24px;
            color: #9191B2;

            &:hover{
                text-decoration: underline;
            }
        }
    }
}
.modal__right-side{
    padding: 44px 60px;
}
.modal-btn{
    border:1px solid #1B69FF;
    margin-left: 25px;
}

.modal__right-title{
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: #2D2D2D;
    margin-bottom: 20px;
}
.modal-form__item-title{
    font-weight: 500;
    font-size: 14px;
    line-height: 36px;
    color: #2D2D2D;
}

.modal__input{
    border:none;
    background: #F4F4F8;
    border: 1px solid #ECECFF;
    box-sizing: border-box;
    border-radius: 12px;
    height: 53px;
    padding: 0 20px;
    outline-color: #1B69FF;
    &::placeholder{
        font-size: 14px;
        line-height: 60px;
        color: #C5C5DD;

    }

    
}
.captcha{
    margin-top: 20px;
    margin-bottom: 12px;
    width: 100%;
    height: 68px;
    background: #FFFFFF;
    box-shadow: 0px 4px 208px #E7E7F3;
    border-radius: 12px;
    border: 1px solid #ECECFF;
}
.forgot-password{
    text-align: center;
    margin-top: 11px;
    font-size: 14px;
    line-height: 24px;
    color: #9191B2;
}
.modal__left-actions-mobile{
    display: none;
}
@media (max-width: 768px){
    .modal__left-description{
        margin-right: 0;
        max-width: 100%;
        font-size: 18px;
        line-height: 33px;

    }
    .modal__left-title{
        font-size: 42px;
    }
    
}

@media (max-width: 600px){
    .modal__input{
        width: 100%;
    }
    .modal__wrapper{
        flex-direction: column;
    }
    .modal__left-title{
        font-size: 23px;
        line-height: 42px;
        max-width: 100%;
    }
    .modal__left-side{
        padding: 25px 25px;
    }
    .modal__left-description{
        font-size: 13px;
        line-height: 26px;
    }
    .modal__right-side{
        padding: 25px 25px;
    }
    .modal__left-actions{
        display: none;
    }
    .modal__left-actions-mobile{
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-top: 31px;
        &--auth{
            flex-direction: row;
            align-items: center;
        }
       
    }
    .modal-btn{
        margin-left: 0;
        margin-top: 17px;
    }
    
    .modal-btn--mobile{
        margin-top: 0;
    }
}