html {
  box-sizing: border-box;
}


*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
 
  background: #F4F4F8;
  margin: 0;
  font-family: "asd", sans-serif;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

.site-container {
  overflow-x: hidden;
}
.sidebar__wrapper {
 
  padding-left: 60px !important;
  padding-top: 30px !important;
  
}
.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}
.body-lock{
  overflow: hidden;
}
.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
button{
  border:none;
  background: transparent;
  cursor: pointer;
  display: block;
  transition: all .2s linear;

  &:hover{
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
}
.header__social-icon{
  transition: all .2s linear;
  &:hover{
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
}
select{
  text-align: center;
}
.btn{
  background: #FFFFFF;
  box-shadow: 0px 20px 50px rgba(27, 105, 255, 0.04);
  border-radius: 12px;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 14px 20px;
  font-weight: 500;
  font-size: 14px;
  cursor: pointer;
  color: #2D2D2D;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  img{
    margin-right: 22px;
  }
  &--mobile{
    display: none;
    img{
      margin-right: 0;
    }
  }
  &--blue {
    background: #1B68FF;
    box-shadow: 0px 20px 50px rgba(27, 105, 255, 0.04);
    color: #fff;
    margin-right: 20px;

    
  }
  &--width {
    width: 223px;
  }
  &--modal{
    width: 100%;
  }
  &--partners__tabs{
    color:#000;
    border: 1px solid #D7D7EB;
    background: transparent;
    border-radius: 12px;
    
}

    
  
  &.partners__active{
    color:#fff;
    background: #1B69FF;
  }
}
.btn__tabs{
  width: 139px !important;
    
  padding: 10px 10px 10px 10px !important;
}
.section__title{
    margin:130px 0 39px 0;
    font-weight: 500;
    font-size: 36px;
    text-align: center;
    line-height: 60px;
    color: #2D2D2D;
}
.cabinet-container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1440px;
}
li{
  list-style: none;
}
ul{
  padding: 0;
}
.clearfix__card{
  cursor: pointer;
  transition: all .2s linear;
  &:hover{
    box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
  }
}
.clearfix__info-card{
  cursor: pointer;
  transition: all .2s linear;
  &:hover{
    box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
  }
}
.clearfix__main-card{
  cursor: pointer;
  transition: all .2s linear;
  &:hover{
    box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
  }
}
.modal__left-actions-mobile{
  display: none;
}
.main__table-mobile{
  td{
    padding-left: 30px;
  }
}

.footer__link{
  &:hover{
    text-decoration: underline;
  }
}